<template>
  <div class="main">
    <van-search
      v-model="searchValue"
      shape="round"
      background="#fff"
      placeholder="请输入搜索关键词"
    />

    <van-popup v-model="showFilterBox" position="right" :style="{height:'100%',width:'80%'}">

    </van-popup>

    <van-card
      tag="标签"
      price="2.00"
      desc="描述信息"
      title="商品标题"
      thumb="https://img01.yzcdn.cn/vant/ipad.jpeg"
      origin-price="10.00"
    />
  </div>
</template>

<script>
export default {
  name: "store",
  components: {},
  data() {
    return {
      searchValue: "",
      showFilterBox: false,
    };
  },
  methods: {},
  beforeMount() {
    let that = this;
    this.$store.commit("setNavConf", {
      title: "商店",
      leftText: "返回",
      rightText: "筛选",
      leftArrow: true,
      clickRight: () => {
        that.showFilterBox = !that.showFilterBox;
      },
    });
  },
};
</script>


